.container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  margin-top: 20px;
  padding: 20px 10px;
}

.container:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: url("./images/image.jpg") no-repeat center;
  background-size: cover;
  z-index: -1;
  filter: blur(50px);
}

.content-box {
  max-width: 850px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #fff;
  box-shadow: 0px 0px 19px 5px rgba(0, 0, 0, 0, 0.19);
}

.left {
  height: 100%;
  background: url("./images/image.jpg") no-repeat center;
  background-size: cover;
}

.right {
  padding: 25px 40px;
}

.right > h1 {
  position: relative;
  font-size: 1.9rem;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.right > h1::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  height: 4px;
  width: 50px;
  border-radius: 2px;
  background-color: black;
}

.field {
  width: 100%;
  padding: 0.5rem 1rem;
  outline: none;
  border: 2px solid rgba(0, 0, 0, 0);
  background-color: rgba(230, 230, 230, 0.6);
  font-size: 1.1rem;
  margin-bottom: 22px;
  transition: 0.3s;
}

.field:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.field:focus {
  background-color: #fff;
  border: 2px solid rgba(30, 85, 250, 0.47);
}

.listButton {
  width: 100%;
  padding: 0.5rem 1rem;
  font-size: 1.1rem;
  background-color: #2ecc71;
  cursor: pointer;
  outline: none;
  color: #fff;
  border: none;
  transition: 0.3s;
}

.right > button:hover {
  background-color: #23ae60;
}

.errorMessage {
  background-color: lightcoral;
  font-size: 1.3rem;
}

@media screen and (max-width: 880px) {
  .content-box {
    grid-template-columns: 1fr;
  }

  .left {
    height: 200px;
  }
}
